<script>
import axios from 'axios';
import Layout from '@layouts/default'
import { authMethods } from '@state/helpers'
import Utils from '../../../../utils/Util';

/**
 * Login component
 */
export default {
	page: {
		title: 'Login Aplikasi Manajemen User',
	},
	components: { Layout },
	data() {
		return {
			api: Utils.Host(),
			username: "",
			password: "",
			message:"",
			alert:false
		}
	},
	mounted() {
    	console.log("alamat", Utils.Host())
  	},
	methods: {
		async Login() {
		try {
			const akun = new URLSearchParams();
			akun.append("username", this.username);
			akun.append("password", this.password);
			let response = await axios.post(this.api+"/auth/login", akun);
			if (response.data.Error_code === 0){
			let token = response.data.Data;
			localStorage.setItem("jwt", token);
			if (token) {
				this.$router.push("dashboard");
			}
			} else {
				this.alert = true
				this.message = response.data.Error_desc
			}
		} catch (err) {
			this.alert = true
			this.message = err.response
		}
		},
	},
}
</script>

<template>
	<Layout>
		<!-- end row -->
		<div class="account-pages my-5">
			<div class="container">
				<div class="row justify-content-center">
					<div class="col-xl-10">
						<div class="card">
							<div class="card-body p-0">
								<div class="row">
									<div class="col-md-6 p-5">
										<div class="mx-auto mb-5">
											<a routerLink="/">
												<img src="@assets/images/logo.png" alt height="24" />
												<h3 class="d-inline align-middle ml-1 text-logo"
													>Manajemen User</h3
												>
											</a>
										</div>

										<b-alert
											v-model="alert"
											variant="danger"
											dismissible
											>{{ message }}</b-alert
										>


											<div class="form-group">
												<div class="input-group input-group-merge">
													<div class="input-group-prepend">
														<span class="input-group-text">
															<feather
																type="mail"
																class="align-middle icon-dual"
															></feather>
														</span>
													</div>
													<b-form-input
														id="input-1"
														v-model="username"
														type="text"
														required
														placeholder="Username"
													></b-form-input>
												</div>
											</div>
											<div class="form-group mt-4">
												<label class="form-control-label">Password</label>
												<div class="input-group input-group-merge">
													<div class="input-group-prepend">
														<span class="input-group-text">
															<feather
																type="lock"
																class="align-middle icon-dual"
															></feather>
														</span>
													</div>
													<b-form-input
														id="password"
														v-model="password"
														type="password"
														required
														placeholder="Password"
													></b-form-input>
												</div>
											</div>
											<b-form-group id="button-group" class="mt-4 mb-1">
												<b-button
													type="submit"
													variant="primary"
													class="btn-block"
													@click="Login"
													>Log In</b-button
												>
											</b-form-group>
										<div class="py-3 text-center">
										</div>
									</div>
									<div class="col-lg-6 d-none d-md-inline-block">
										<img src="@assets/images/banner.jpg" width="100%" />
									</div>
								</div>
							</div>
							<!-- end card-body -->
						</div>
						<!-- end card -->

						<!-- end row -->
					</div>
					<!-- end col -->
				</div>
				<!-- end row -->
			</div>
			<!-- end container -->
		</div>
	</Layout>
</template>

